import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, Modal, Alert, Image, Platform } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { supabase } from '../utils/supabase';
import { spacing } from '../theme/spacing';
import { GoogleSignIn } from './GoogleSignIn';
import { Session, User } from '@supabase/supabase-js';
import { QuiversMenu } from './QuiversMenu';
import { QuiverManager } from './QuiverManager';
import { useTheme } from '../theme/ThemeContext';

export const UserMenu = () => {
  const { colors } = useTheme();
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [session, setSession] = useState<Session | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [isAuthModalVisible, setIsAuthModalVisible] = useState(false);
  const [isQuiversModalVisible, setIsQuiversModalVisible] = useState(false);

  useEffect(() => {
    // Get initial session
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      setUser(session?.user ?? null);
    });

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      setUser(session?.user ?? null);
      if (session) {
        console.log('User logged in:', session.user);
      }
    });

    return () => subscription.unsubscribe();
  }, []);

  const handleSignOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      setSession(null);
      setUser(null);
      setIsMenuVisible(false);
    } catch (error) {
      Alert.alert('Error', 'Failed to sign out');
      console.error('Sign out error:', error);
    }
  };

  const renderAuthModal = () => (
    <Modal
      visible={isAuthModalVisible}
      transparent
      animationType="fade"
      onRequestClose={() => setIsAuthModalVisible(false)}
    >
      <TouchableOpacity
        style={[styles.modalOverlay, { backgroundColor: 'rgba(0, 0, 0, 0.5)' }]}
        activeOpacity={1}
        onPress={() => setIsAuthModalVisible(false)}
      >
        <View 
          style={[styles.modalContent, { backgroundColor: colors.background.primary }]} 
          onStartShouldSetResponder={() => true}
        >
          <View style={[styles.modalHeader, { borderBottomColor: colors.border }]}>
            <Text style={[styles.modalTitle, { color: colors.text.primary }]}>Sign In</Text>
            <TouchableOpacity onPress={() => setIsAuthModalVisible(false)}>
              {Platform.OS === 'web' ? (
                <Text style={{ fontSize: 24, color: colors.text.primary }}></Text>
              ) : (
                <Ionicons name="close" size={24} color={colors.text.primary} />
              )}
            </TouchableOpacity>
          </View>
          <GoogleSignIn onSuccess={() => setIsAuthModalVisible(false)} />
        </View>
      </TouchableOpacity>
    </Modal>
  );

  if (!session) {
    return (
      <View style={styles.container}>
        <TouchableOpacity
          style={[styles.signInButton, { backgroundColor: colors.primary }]}
          onPress={() => setIsAuthModalVisible(true)}
        >
          <Text style={[styles.signInText, { color: colors.text.inverse }]}>Sign In</Text>
        </TouchableOpacity>
        {renderAuthModal()}
      </View>
    );
  }

  return (
    <View style={styles.container}>
      <TouchableOpacity
        style={styles.profileButton}
        onPress={() => setIsMenuVisible(!isMenuVisible)}
      >
        {user?.user_metadata?.avatar_url ? (
          <Image 
            source={{ uri: user.user_metadata.avatar_url }} 
            style={styles.avatarImage}
          />
        ) : Platform.OS === 'web' ? (
          <Text style={{ fontSize: 24, color: colors.text.primary }}></Text>
        ) : (
          <Ionicons name="person-circle-outline" size={24} color={colors.text.primary} />
        )}
      </TouchableOpacity>
      
      {isMenuVisible && (
        <View style={[styles.menu, { 
          backgroundColor: colors.background.primary,
          borderColor: colors.border,
        }]}>
          <View style={styles.userInfo}>
            <Text style={[styles.userName, { color: colors.text.primary }]}>
              {user?.user_metadata?.full_name || user?.email}
            </Text>
            <Text style={[styles.userEmail, { color: colors.text.secondary }]}>
              {user?.email}
            </Text>
          </View>
          <View style={[styles.menuDivider, { backgroundColor: colors.border }]} />
          <View style={styles.menuItem}>
            <QuiversMenu onPress={() => {
              setIsQuiversModalVisible(true);
              setIsMenuVisible(false);
            }} />
          </View>
          <TouchableOpacity style={styles.menuItem} onPress={handleSignOut}>
            {Platform.OS === 'web' ? (
              <Text style={{ fontSize: 20, color: colors.text.primary }}></Text>
            ) : (
              <Ionicons name="log-out-outline" size={20} color={colors.text.primary} />
            )}
            <Text style={[styles.menuItemText, { color: colors.text.primary }]}>Sign Out</Text>
          </TouchableOpacity>
        </View>
      )}

      <Modal
        visible={isQuiversModalVisible}
        transparent
        animationType="fade"
        onRequestClose={() => setIsQuiversModalVisible(false)}
      >
        <View 
          style={[
            styles.modalOverlay,
            { backgroundColor: 'rgba(0, 0, 0, 0.5)' }
          ]}
          onStartShouldSetResponder={() => true}
        >
          <View 
            style={[
              styles.modalContent,
              { backgroundColor: colors.background.primary }
            ]}
          >
            <QuiverManager
              selectedFoils={[]}
              onClose={() => setIsQuiversModalVisible(false)}
              initialTab="saved"
              standalone={true}
            />
          </View>
        </View>
      </Modal>

      {renderAuthModal()}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'relative',
    zIndex: 2100,
  },
  signInButton: {
    paddingHorizontal: 16,
    paddingVertical: 8,
    borderRadius: 8,
  },
  signInText: {
    fontWeight: '600',
  },
  profileButton: {
    padding: 8,
    zIndex: 2100,
  },
  avatarImage: {
    width: 32,
    height: 32,
    borderRadius: 16,
  },
  menu: {
    position: 'absolute',
    top: '100%',
    right: 0,
    borderRadius: 8,
    padding: 8,
    minWidth: 240,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
    borderWidth: 1,
    zIndex: 2100,
  },
  userInfo: {
    padding: 12,
  },
  userName: {
    fontSize: 16,
    fontWeight: '600',
    marginBottom: 4,
  },
  userEmail: {
    fontSize: 14,
  },
  menuDivider: {
    height: 1,
    marginVertical: 8,
  },
  menuItem: {
    flexDirection: 'row',
    alignItems: 'center',
    padding: 12,
    gap: 8,
  },
  menuItemText: {
    fontSize: 16,
  },
  modalOverlay: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    justifyContent: 'center',
    alignItems: 'center',
    padding: spacing.LARGE,
    zIndex: 2200,
  },
  modalContent: {
    width: '100%',
    maxWidth: 600,
    borderRadius: 12,
    maxHeight: '90%',
    overflow: 'hidden',
  },
  modalHeader: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: spacing.LARGE,
    borderBottomWidth: 1,
  },
  modalTitle: {
    fontSize: 20,
    fontWeight: '600',
  },
  modalBody: {
    padding: spacing.LARGE,
  },
}); 