import React from 'react';
import { View, Text, StyleSheet, TouchableOpacity, Platform } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { useTheme } from '../theme/ThemeContext';
import { useQuivers } from '../hooks/useQuivers';

interface QuiversMenuProps {
  onPress: () => void;
}

export const QuiversMenu: React.FC<QuiversMenuProps> = ({ onPress }) => {
  const { colors } = useTheme();
  const { quivers } = useQuivers();

  return (
    <TouchableOpacity onPress={onPress}>
      <View style={styles.menuItemContent}>
        {Platform.OS === 'web' ? (
          <Text style={[styles.menuItemText, { color: colors.text.primary }]}>
            My Quivers {quivers.length > 0 ? `(${quivers.length})` : ''}
          </Text>
        ) : (
          <>
            <Ionicons 
              name="bookmark-outline" 
              size={20} 
              color={colors.text.primary}
            />
            <Text style={[styles.menuItemText, { color: colors.text.primary }]}>
              My Quivers {quivers.length > 0 ? `(${quivers.length})` : ''}
            </Text>
          </>
        )}
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  menuItemContent: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 8,
  },
  menuItemText: {
    fontSize: 16,
  },
}); 