import React, { useRef } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createNavigationContainerRef, LinkingOptions, getStateFromPath, getPathFromState } from '@react-navigation/native';
import { HelmetProvider } from 'react-helmet-async';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import { ErrorBoundary } from './src/components/ErrorBoundary';
import { ThemeProvider } from './src/theme/ThemeContext';
import { AppProvider } from './src/context/AppContext';
import { LoadingIndicator } from './src/components/LoadingIndicator';
import { routes } from './src/utils/routes';
import { RootStackParamList } from './src/types/navigation';
import { getPrefixes } from './src/config/environment';
import { AppHeader } from './src/navigation/AppHeader';
import { Platform } from 'react-native';

export default function App() {
  const navigationRef = createNavigationContainerRef<RootStackParamList>();
  
  const linking: LinkingOptions<RootStackParamList> = {
    prefixes: getPrefixes(),
    config: {
      screens: {
        Browse: {
          path: '/',
          parse: {
            mode: (mode: string) => mode,
          },
          stringify: {
            mode: (mode: string) => mode,
          },
        },
        Comparison: {
          path: '/comparison',
          exact: true,
          stringify: {
            quiverName: (name: string) => name,
            foils: (foils: any[]) => JSON.stringify(foils),
          },
        },
        Quiver: {
          path: '/quiver/:quiverId',
          parse: {
            quiverId: (id: string) => id,
            quiverName: (name: string) => name,
            foils: (foils: string) => foils ? JSON.parse(foils) : undefined,
          },
          stringify: {
            quiverId: (id: string) => id,
            quiverName: (name: string) => name,
            foils: (foils: any[]) => JSON.stringify(foils),
          },
        },
        Terms: '/terms',
        Privacy: '/privacy',
        Contact: '/contact',
      },
    },
    getStateFromPath: (path, options) => {
      // Handle direct navigation to comparison
      if (path === '/comparison') {
        return {
          routes: [
            {
              name: 'Comparison',
              params: {},
            },
          ],
        };
      }
      return getStateFromPath(path, options);
    },
    getPathFromState: (state, config) => {
      // Handle comparison route specially
      if (state.routes[state.routes.length - 1].name === 'Comparison') {
        return '/comparison';
      }
      const path = getPathFromState(state, config);
      return path || '/';
    },
  };

  return (
    <ThemeProvider>
      <AppProvider>
        <HelmetProvider>
          <ErrorBoundary>
            <SafeAreaProvider>
              <NavigationContainer 
                ref={navigationRef}
                linking={linking}
                fallback={<LoadingIndicator />}
              >
                <AppHeader />
              </NavigationContainer>
            </SafeAreaProvider>
          </ErrorBoundary>
        </HelmetProvider>
      </AppProvider>
    </ThemeProvider>
  );
} 